<template lang="html">
    <div class="utilizations">
        <div id='main'>

        </div>
    </div>
</template>

<script>
import Swal from 'sweetalert2'
var echarts = require('echarts');
export default {
    name: 'Utilizations',
    data: function() {
        return {
            time: [],
            utilizationsList: []
            // meanTime: [2,3,4,5,6,6,1],
            // numList: [2,3,4,5,6,6,1]
        }
    },
    mounted: function() {
        this.getList()
        this.generateGraph()
    },
    methods: {
        getList: function() {

            var len = this.GLOBAL.schedCore.running.serverLog.length
            for(var m = 0; m < this.GLOBAL.schedCore.config.serverCluster; m++) {
                var utilObj = {
                    "name": 1,
                    "type": "bar",
                    "stack": "sta1",
                    "label": {
                        "normal": {
                            "show": true,
                            "position": "inside"
                        }
                    },
                    "color": '#'+(Math.random()*0xFFFFFF<<0).toString(16),
                    "data": []
                }
                this.utilizationsList.push(
                    utilObj
                )
            }
            for (var i = 0; i < len; i++) {
                var obj = this.GLOBAL.schedCore.running.serverLog[i]
                this.time.push(obj.time.toFixed(5))
                for(var j = 0; j < this.GLOBAL.schedCore.config.serverCluster; j++) {
                    if(j >= obj.list.length) {
                        this.utilizationsList[j].data.push('-')
                    }else {
                        this.utilizationsList[j].data.push({
                            name: obj.list[j].name,
                            value: obj.list[j].number
                        })
                    }
                }
            }
            // console.log(this.utilizationsList)
        },
        changeGlobalData: function(val) {

        },
        generateGraph: function() {
            var myChart = echarts.init(document.getElementById('main'))

            // var date = [];
            //
            // var data = [];

            var that = this
            myChart.setOption({
                "animation": true,
                "title": {
                    "text": "Utilizations",
                    "left": "center",
                    "textStyle": {
                        color: '#c4c6cd'
                    }
                    // "textVerticalAlign": "center"
                },
                "tooltip": {
                    "trigger": "axis",
                    "axisPointer": {
                        "type": "shadow"
                    },
                    "triggerOn": "mousemove|click"
                },
                "textStyle": {
                    color: 'white',
                    fontSize: 14
                },
                "dataZoom": [{
                    "show": true,
                    "type": "slider",
                    "start": 0,
                    "end": 1,
                    "orient": "horizontal"
                }, {
                    "show": true,
                    "type": "inside",
                    "start": 50,
                    "end": 95,
                    "orient": "horizontal"
                }],
                "grid": {
                    "left": "3%",
                    "right": "10%",
                    "bottom": "7%",
                    "containLabel": true
                },
                "yAxis": {
                    "type": "value",
                    "label": "Number",
                    "name": "Count",
                    "nameLocation": "center",
                    "nameGap": 40
                },
                "xAxis": {
                    "type": "category",
                    "name": "Time",
                    "nameLocation": "center",
                    "nameGap": 40,
                    "data": that.time,
                    "axisTick": {
                        "alignWithLabel": false
                    },
                    "boundaryGap": true,
                    "splitLine": {
                        "show": false
                    }
                },
                "series": this.utilizationsList
            })

            myChart.on('dblclick', function (params) {
                var index = params.dataIndex
                this.popupDetail(index)
            })


        },

        popupDetail: function(index) {
            // console.log(that.GLOBAL.schedCore.running.serverLog[index])

        }

    },
    components: {

    }
}
</script>

<style lang="css" scoped>
.utilizations {
    width: 100%;
    height: 100%;
}
#main {
    width: 100%;
    height: 60%;
    /* margin-top: 20px; */
}

</style>
