            var res = []
            var i = 0
            var totalLen = this.GLOBAL.schedCore.running.completedQueue.length
            if(totalLen > 11) {
                i = totalLen - 11
            }
            for(var j = i; j < totalLen; j++) {
                res.push(this.GLOBAL.schedCore.running.completedQueue[j].size.toFixed(4))
            }
            return res
        },
        getLastElevenMeanTime: function() {
            var res = []
            var i = 0
            var totalLen = this.GLOBAL.schedCore.running.meanTimeLog.length
            if(totalLen > 11) {
