            for(var i = 0; i < len; i++) {
                if(flag =='meanTime') {
                    res.push(this.GLOBAL.schedCore.running.meanTimeLog[i].meanTime)
                }else {
                    res.push(this.GLOBAL.schedCore.running.meanTimeLog[i].num)
                }
            }
            return res;
        },
        changeGlobalData: function (val) {

        },
        generateGraph: function() {
            var myChart = echarts.init(document.getElementById('main'));

            // var date = [];
            //
            // var data = [];


