<template lang="html">
    <div class="gloabl">

    </div>
</template>

<script>
var schedCore = {
    config: {
        jobs: [
            {
                sizeFun: 'deterministic',
                sizeFunPara: '1',
                speedupFun: 'power',
                speedupFunPara: '0.5',
                color: '#e38f61',
                id: '0',
                proportionVal: 10
            },

            {
                sizeFun: 'exponential',
                sizeFunPara: '1',
                speedupFun: 'power',
                speedupFunPara: '0.5',
                color: '#fdbb3a',
                id: '2',
                proportionVal: 10
            },
            {
                sizeFun: 'uniform',
                sizeFunPara: '0,2',
                speedupFun: 'power',
                speedupFunPara: '0.5',
                color: '#589fed',
                id: '1',
                proportionVal: 10
            },
        ],
        // {
        //     sizeFun: 'deterministic',
        //     sizeFunPara: '0.1,0.3',
        //     speedupFun: 'power',
        //     speedupFunPara: '1'
        // }
        arrivalRate: 10.0,
        serverCluster: 16,
        algorithmName: "equi",
        algorithm: ""
        // algorithm: 'function runScheduler() { serverCluster.printCurrentStatus(); if(queue.length > 0) { var task = queue.dequeue(); var numOfAvailableNodes = serverCluster.checkAvailableNum(); if(numOfAvailableNodes < task.nodeNum) { serverCluster.preemption(task.nodeNum); } serverCluster.process(task); serverCluster.checkAllTasks(); }else { serverCluster.checkAllTasks(); } if(completedQueue.length > 0) { colorLog(completedQueue.length.toString() + " tasks have been done!!", "success"); } if(completedQueue.length == totalNum) { clearInterval(handler); colorLog("All the tasks have been done!", "error"); colorLog("Takes " + (getCurrentTime() - curr) / 1000.0.toString() + " seconds", "error"); } }',

    },
    status: false,
    globalTime: 0,
    running: {
        // jobs
        queue: [],
        jobsDict: {},
        lastEleventJobs:[],
        completedQueue: [],
        // completed total time
        totalCostTimeForCompletedJobs: 0,
        // job generators queue
        jobGeneratorQueue: [],
        totalJobsNum: 0,
        currentMeanTime:0,
        // server
        availableNodeNum: 0, // need to be Initialized
        serverTaskList: [],
        // {
        //     time: 0,
        //     usedNumber: 10,
        //     list: [{
        //         name: 'name',
        //         number: 2
        //     }]
        // },
        serverLog: [],
        // {
        //     totalTime: 0,
        //     num: 0,
        //     meanTime: 0
        // }
        meanTimeLog:[],




    }

}


var Objects = {

    JobGenerator: function(jobsConfig) {
        this.id = jobsConfig.id
        this.sizeFun = jobsConfig.sizeFun
        this.sizeFunPara = jobsConfig.sizeFunPara
        this.speedupFun = jobsConfig.speedupFun
        this.speedupFunPara = jobsConfig.speedupFunPara
        this.color = jobsConfig.color
        this.totalNumber = 0
        this.totalSize = 0
        this.proportionVal = jobsConfig.proportionVal
        this.nextGenerateTime = schedCore.globalTime + getNextExponentialValue(jobsConfig.proportionVal)
        // {
        //     time: 0,
        //     size: 0,
        //     id: 0,
        // }
        this.log = []

    },

    Job: function (id, sizeFun, sizeFunPara, speedupFun, speedupFunPara, jobClass, generateTime) {
        this.id = id
        this.size = getJobSize(sizeFun, sizeFunPara)
        this.currentSize = this.size
        this.jobClass = jobClass
        this.speedupFun = speedupFun
        this.speedupFunPara = speedupFunPara
        this.name = jobClass + '_' + id
        this.usedNodeNumber = 0
        this.speedup = speedUpFunc(speedupFun, speedupFunPara, this.usedNodeNumber)
        this.leftTime = Infinity
        this.generateTime = generateTime
        this.completedTime = 0
        // {
        //     time: 0,
        //     size: 10,
        // }
        this.log = []
    }
}

// global


function shedulingAlgorithm(schedCore, queue, serverInfo, jobsDict, completedQueue, jobGeneratorQueue, currentTime) {
    // console.log(schedCore)
    serverInfo.serverTaskList.sort(function(a, b){
        return a.leftTime - b.leftTime
    })
    var doneJobs = []
    var allowRunTime = 0
    var currentAvailableNodeNum = serverInfo.availableNodeNum
    if(serverInfo.serverTaskList.length != 0) {
        doneJobs.push(serverInfo.serverTaskList[0]);
        for(var i = 1; i < serverInfo.serverTaskList.length; i++) {
            if(serverInfo.serverTaskList[0].leftTime == serverInfo.serverTaskList[i].leftTime) {
                doneJobs.push(serverInfo.serverTaskList[i])
            }else {
                break;
            }
        }
    }
    if(doneJobs.length != 0) {
        allowRunTime = doneJobs[0].leftTime
    }
    for(var j = 0; j < doneJobs.length; j++) {
        currentAvailableNodeNum += doneJobs[j].usedNodeNumber
        var currJob = doneJobs[0]
        currJob.currentSize = 0
        currJob.leftTime = 0
        currJob.usedNodeNumber = 0
        currJob.speedup = 0
        currJob.completedTime = currentTime + allowRunTime
        currJob.log.push({
            time: currentTime + allowRunTime,
            size: 0
        })
        schedCore.running.totalCostTimeForCompletedJobs += currJob.completedTime - currJob.generateTime
        schedCore.running.currentMeanTime = getMeanTime(schedCore)

        schedCore.running.meanTimeLog.push(
            {
                totalTime: schedCore.running.totalCostTimeForCompletedJobs,
                num: schedCore.running.completedQueue.length,
                meanTime: schedCore.running.currentMeanTime
            }
        )
        completedQueue.push(currJob)
        serverInfo.serverTaskList.shift()
    }
    // handle remaining jobs in the task list
    for(var k = 0; k < serverInfo.serverTaskList.length; k++) {
        var currJob_1 = serverInfo.serverTaskList[k]
        currJob_1.currentSize -= currJob_1.speedup * allowRunTime
        currJob_1.leftTime = currJob_1.currentSize / currJob_1.speedup
    }
    var newJobs = []
    var newJobsObj = getNewJobs(schedCore, jobGeneratorQueue, allowRunTime)

    newJobs = newJobsObj.newJobs
    queue = queue.concat(newJobs)

    // add new jobs to queue

    while(currentAvailableNodeNum > 0){
        if(queue.length > 0) {
            serverInfo.serverTaskList.push(queue[0])
            queue.shift()
            currentAvailableNodeNum -= 1
        }else {
            break
        }
    }

    currentTime += allowRunTime
    if(currentTime < newJobsObj.currentTime) {
        currentTime = newJobsObj.currentTime
    }

    // reassign variable for jobs
    if(serverInfo.serverTaskList.length != 0) {
        currentAvailableNodeNum = 0
        var mod = serverInfo.serverNum % serverInfo.serverTaskList.length
        var nodeNum = Math.floor(serverInfo.serverNum / serverInfo.serverTaskList.length)

        for(var l = 0; l < serverInfo.serverTaskList.length; l++) {
            var currJob_2 = serverInfo.serverTaskList[l]
            currJob_2.usedNodeNumber = nodeNum
            currJob_2.speedup = speedUpFunc(currJob_2.speedupFun, currJob_2.speedupFunPara, nodeNum)
            currJob_2.leftTime = currJob_2.currentSize / currJob_2.speedup
            currJob_2.log.push({
                time: currentTime,
                size: currJob_2.currentSize,
            })
        }
        if(mod != 0) {
            serverInfo.serverTaskList[0].usedNodeNumber += mod
            serverInfo.serverTaskList[0].speedup = speedUpFunc(serverInfo.serverTaskList[0].speedupFun, serverInfo.serverTaskList[0].speedupFunPara, serverInfo.serverTaskList[0].usedNodeNumber)
        }
    }
    // console.log(serverInfo.serverTaskList)



    var slog = {
        time: currentTime,
        usedNumber: serverInfo.serverNum - currentAvailableNodeNum,
        list: []
    }
    for(var q = 0; q < serverInfo.serverTaskList.length; q++) {
        slog.list.push({
            name: serverInfo.serverTaskList[q].name,
            number: serverInfo.serverTaskList[q].usedNodeNumber
        })
    }
    schedCore.running.serverLog.push(slog)
    var res = {
        schedCore: schedCore,
        queue: queue,
        serverInfo: {
                serverTaskList: serverInfo.serverTaskList,
                serverNum: serverInfo.serverNum,
                availableNodeNum: currentAvailableNodeNum,
        },
        jobsDict: jobsDict,
        completedQueue: completedQueue,
        jobGeneratorQueue: jobGeneratorQueue,
        currentTime: currentTime,
        serverLog: schedCore.running.serverLog
    }
    return res

}

// local


function getNextExponentialValue(para) {
    var rateParameter = 1.0;
    if(para != 1 && para != 0) rateParameter = para;
    return -Math.log(1.0 - Math.random()) / rateParameter;
}

function getJobSize(sizeFun, sizeFunPara) {
    var arr = sizeFunPara.split(',')
    for(var i = 0; i < arr.length; i++) {
        arr[i] = parseFloat(arr[i])
    }
    var res = 0
    while(res < 0.000001) {
        if(sizeFun == 'exponential') {
            res = exponential(arr)
        }else if(sizeFun == 'deterministic') {
            res = deterministic(arr)
        }else if(sizeFun == 'uniform') {
            res = uniform(arr)
        }else if(sizeFun == 'erlang') {
            res = erlang(arr)
        }else if(sizeFun == 'pareto') {
            res = pareto(arr)
        }
    }
    return res

}

function speedUpFunc(name, para, k) {
    para = parseFloat(para)
    if(k == 0) return 0
    if(name == 'power') {
        return Math.pow(k, para) * 1.0
    }else if(name == 'amdahl') {
        return 1/(1 - para + para/k) * 1.0
    }else  {
        return para * 1.0
    }
}

function getNewJobs(schedCore, jobGeneratorQueue, allowRunTime) {
    // return
    // {
    //     currentTime: 0,
    //     newJobs: []
    // }
    jobGeneratorQueue.sort(function(a, b) {
        return a.nextGenerateTime - b.nextGenerateTime
    })

    var res = {
        currentTime: 0,
        newJobs: []
    }

    if(allowRunTime == 0) {
        res.currentTime = jobGeneratorQueue[0].nextGenerateTime
        var job1 = new Objects.Job(
            // id
            jobGeneratorQueue[0].totalNumber.toString(),
            // sizeFun
            jobGeneratorQueue[0].sizeFun,
            jobGeneratorQueue[0].sizeFunPara,
            jobGeneratorQueue[0].speedupFun,
            jobGeneratorQueue[0].speedupFunPara,
            jobGeneratorQueue[0].id,
            res.currentTime
        )
        res.newJobs.push(job1)
        schedCore.running.jobsDict[job1.name] = job1
        jobGeneratorQueue[0].log.push({
            time: job1.generateTime,
            size: job1.size,
            id: job1.id
        })
        jobGeneratorQueue[0].totalNumber += 1
        jobGeneratorQueue[0].totalSize += job1.size
        jobGeneratorQueue[0].nextGenerateTime = res.currentTime + getNextExponentialValue(jobGeneratorQueue[0].proportionVal)
    }else {
        var allowAllTime = schedCore.globalTime + allowRunTime

        while(allowAllTime >= jobGeneratorQueue[0].nextGenerateTime) {
            var job2 = new Objects.Job(
                jobGeneratorQueue[0].totalNumber.toString(),
                jobGeneratorQueue[0].sizeFun,
                jobGeneratorQueue[0].sizeFunPara,
                jobGeneratorQueue[0].speedupFun,
                jobGeneratorQueue[0].speedupFunPara,
                jobGeneratorQueue[0].id,
                jobGeneratorQueue[0].nextGenerateTime
            )
            res.newJobs.push(job2)
            schedCore.running.jobsDict[job2.name] = job2
            jobGeneratorQueue[0].log.push({
                time: job2.generateTime,
                size: job2.size,
                id: job2.id
            })
            jobGeneratorQueue[0].totalNumber += 1
            jobGeneratorQueue[0].totalSize += job2.size
            jobGeneratorQueue[0].nextGenerateTime += getNextExponentialValue(jobGeneratorQueue[0].proportionVal)

            jobGeneratorQueue.sort(function(a, b) {
                return a.nextGenerateTime - b.nextGenerateTime
            })

        }
    }
    schedCore.running.totalJobsNum += res.newJobs.length
    // console.log(schedCore)
    // update running.lastEleventJobs
    var lastEleventJobs = []
    var count = 11
    for(var m = res.newJobs.length - 1; m >= 0 && count > 0; m--) {
        lastEleventJobs.push(res.newJobs[m].size.toFixed(4))
        count--
    }
    for(var k = schedCore.running.lastEleventJobs.length - 1; k >= 0 && count > 0; k--) {
        // console.log(schedCore.running.lastEleventJobs[k])
        lastEleventJobs.push(schedCore.running.lastEleventJobs[k])
        count--
    }
    var left = 0
    var right = lastEleventJobs.length - 1
    while(right > left) {
        var tem = lastEleventJobs[left]
        lastEleventJobs[left] = lastEleventJobs[right]
        lastEleventJobs[right] = tem
        right--
        left++
    }
    schedCore.running.lastEleventJobs = lastEleventJobs
    return res
}

function exponential(arr) {
    var rateParameter = arr[0];
    if(arr != 1 && arr.length == 0) rateParameter = (1 / 40);
    return -Math.log(1.0 - Math.random()) / rateParameter;
}

function deterministic(arr) {
    var c = 1;
    if(arr.length != 0) c = arr[0];
    return c;
}

function uniform(arr) {
    var a = 0;
    var b = 2;
    if(arr.length != 0) {
        a = arr[0];
        b = arr[1];
    }
    return a + Math.random() * (b-a);
}

function erlang(arr) {
    var n = 2;
    var lambda = 1;
    if(arr.length != 0) {
        n = arr[0];
        lambda = arr[1];
    }
    var total = 0.0;
    for (var i = 0; i < n; i++)
        total += -Math.log(1-Math.random()) / lambda;
    return total;
}

function pareto(arr) {
    var alpha = alpha | 2;
    var sigma = sigma | 1;
    var mu = mu | sigma;
    if(arr.length != 0) {
        alpha = arr[0];
        sigma = arr[1];
        mu = arr[2];
    }
    return mu + sigma * (Math.pow(1-Math.random(), -1/alpha) - 1);
}

function getMeanTime(schedCore) {
    if(schedCore.running.completedQueue.length != 0) {
        return (schedCore.running.totalCostTimeForCompletedJobs / schedCore.running.completedQueue.length)
    }
    return 0.0
}


export default {
    name: 'globalVariables',
    schedCore,
    Objects,
    shedulingAlgorithm
}
</script>

