<template lang="html">
    <div class="index" id="home">
        <div id="main-content">
            <div id="content" v-show="firstLevel==0">
                <div id="content-title">
                    <h3 id="from-here">Simulation System</h3>
                    <p id="welcome">Welcome, please choose one of the options</p>
                </div>
                <div id="options">
                    <div id="blank-button"></div>
                    <button id="first-button" type="button" name="button" class="option-button" @click="getFirstPagesByIndex(1)">Start A New Simulation</button>
                    <button type="button" name="button" class="option-button" @click="getFirstPagesByIndex(2)">Import An Existed Simulation</button>
                </div>
            </div>
            <transition-group name="bounce">
                <div class="start-container" v-show="firstLevel==1" v-bind:key="firstLevel">
                    <div id="start-container-tab">
                        <a class="start-tab" v-bind:class="{ 'start-tab-active': !noJobs }">
                            Job
                        </a>
                        <a class="start-tab" v-bind:class="{ 'start-tab-active': !noArrivalRate }">
                            Arrival Rate
                        </a>
                        <a class="start-tab" v-bind:class="{ 'start-tab-active': !noServerCluster }">
                            Server
                        </a>
                        <a class="start-tab" v-bind:class="{ 'start-tab-active': !noAlgorithm }">
                            Scheduling Algorithm
                        </a>
                    </div>

                    <div id="start-table-container" v-show="newSimulationPage==0">
                        <div id="start-table-add">
                            <a id="start-add" @click="popUpConfigure()">Add</a>
                        </div>
                        <div id="start-table-title">
                            <table class="table-content">
                                <tr>
                                    <td class="table-td">Job Class</td>
                                    <td class="table-td">Job Size Distribution</td>
                                    <td class="table-td">Speedup Function</td>
                                    <td class="table-td">Proportion</td>
                                    <td class="table-td">Operation</td>
                                </tr>
                            </table>

                        </div>
                        <div id="start-table-content">
                            <transition-group name="list" tag="p">
                                <table class="table-content" v-for="(job, index) in currJobs" v-bind:key="index">
                                    <tr>
                                        <td class="table-td">Class_{{index + 1}}</td>
                                        <td class="table-td">{{job.sizeFun}}<br>Parameters: {{job.sizeFunPara}}</td>
                                        <td class="table-td">{{job.speedupFun}}<br>Parameters: {{job.speedupFunPara}}</td>
                                        <td class="table-td proportion"><input type="number" min="1" v-model.number="job.proportionVal" value="1"></td>
                                        <td class="table-td" @click="deleteAJobByIndex(index)"><a>Delete</a></td>
                                    </tr>
                                </table>
                            </transition-group>
                        </div>

                        <div class="last-next">
                            <button type="button" name="button" class="last-next-button" style="float:left" @click="getFirstPagesByIndex(0)">&larr; Last</button>
                            <button type="button" name="button" class="last-next-button" :disabled='noJobs' @click="getPagesByIndex(1)" v-bind:class="{ 'last-next-button-noactive': noJobs }" style="float:right">Next &rarr;</button>
                        </div>

                    </div>

                    <div id="start-rate-container" v-show="newSimulationPage==1">
                        <div class="rate-server-input">
                            <span>Arrival Rate <br><span style="font-size:12px;">Arrival rate ùúÜ‚â•0 of new jobs</span><br><br></span>
                            <input type="text" class="rate-server" placeholder="Arrival rate ùúÜ‚â•0 of new jobs" v-model="arrivalRate">
                        </div>
                        <div class="last-next">
                            <button type="button" name="button" class="last-next-button" style="float:left" @click="getPagesByIndex(0)">&larr; Last</button>
                            <button type="button" name="button" class="last-next-button" :disabled='noArrivalRate' @click="getPagesByIndex(2)" v-bind:class="{ 'last-next-button-noactive': noArrivalRate }" style="float:right">Next &rarr;</button>
                        </div>
                    </div>

                    <div id="start-server-container" v-show="newSimulationPage==2">
                        <div class="rate-server-input">
                            <span>Server Cluster <br><span style="font-size:12px;">Only a single parameter: number of servers ùëÅ > 0</span><br><br></span>
                            <input type="text" class="rate-server" placeholder="Only a single parameter: number of servers ùëÅ > 0" v-model="serverCluster">
                        </div>
                        <div class="last-next">
                            <button type="button" name="button" class="last-next-button" style="float:left" @click="getPagesByIndex(1)">&larr; Last</button>
                            <button type="button" name="button" class="last-next-button" :disabled='noServerCluster' @click="getPagesByIndex(3)" v-bind:class="{ 'last-next-button-noactive': noServerCluster }" style="float:right">Next &rarr;</button>
                        </div>
                    </div>

                    <div id="start-algorithm-container" v-show="newSimulationPage==3">
                        <div class="rate-algorithm-select">
                            <select class="algorithm-select" @change="algorithmNameChange()" v-model="algorithmName">
                                <option value="equi">EQUI</option>
                                <option value="selfdefined">Self-defined</option>
                            </select>
                        </div>

                        <textarea class="textarea" v-model="algorithm" placeholder="Please input the algorithm"></textarea>


                        <div class="last-next">
                            <button type="button" name="button" class="last-next-button" style="float:left" @click="getPagesByIndex(2)">&larr; Last</button>
                            <button type="button" name="button" class="last-next-button" :disabled='noAlgorithm' @click="getPagesByIndex(4)" v-bind:class="{ 'last-next-button-noactive': noAlgorithm }" style="float:right;background-color:#36bea6;">Simulate</button>
                        </div>
                    </div>


                </div>

                <div class="start-container" v-show="firstLevel==2" v-bind:key="firstLevel + 100">
                    <file-pond
                        name="test"
                        ref="pond"
                        label-idle="Click to upload file..."
                        v-bind:allow-multiple="false"
                        accepted-file-types="application/json"
                        v-bind:files="myFiles"
                        v-on:addfile="handleFileAdded"
                        v-on:removefile="handleFileRemove"
                        v-on:init="handleFilePondInit"/>
                    <div class="processing">
                        <img v-show="fileContent.length != 0" src="../assets/img/processing.gif" alt="">
                    </div>
                    <div class="last-next">
                        <button type="button" name="button" class="last-next-button" style="margin:auto" @click="getFirstPagesByIndex(0)">Back</button>
                    </div>
                </div>
            </transition-group>
        </div>
        <!-- <vue-particles color="#fff"></vue-particles> -->
    </div>
</template>

<script>
import Swal from 'sweetalert2'
// Import Vue FilePond
import vueFilePond from 'vue-filepond'

// Import FilePond styles
import 'filepond/dist/filepond.min.css'

// Import FilePond plugins
// Please note that you need to install these plugins separately

// Import image preview plugin styles
// import 'filepond-plugin-image-preview/dist/filepond-plugin-image-preview.min.css'

// Import image preview and file type validation plugins
import FilePondPluginFileValidateType from 'filepond-plugin-file-validate-type'
// import FilePondPluginImagePreview from 'filepond-plugin-image-preview'

// Create component
const FilePond = vueFilePond(
      FilePondPluginFileValidateType,
      // FilePondPluginImagePreview
)



window.option1Change = function () {
    var obj = document.getElementById('swal-select1')
    var obj_input = document.getElementById('swal-input1')
    if(obj.value == 'deterministic') {
        obj_input.value = '1'
    }else if(obj.value == 'uniform') {
        obj_input.value = '0,2'
    }else if(obj.value == 'exponential') {
        obj_input.value = '1'
    }else if(obj.value == 'erlang') {
        obj_input.value = '2,1'
    }else if(obj.value == 'pareto') {
        obj_input.value = '2,1,2'
    }
}

window.option2Change = function () {
    var obj = document.getElementById('swal-select2')
    var obj_input = document.getElementById('swal-input2')
    if(obj.value == 'power') {
        obj_input.value = '0.5'
    }else if(obj.value == 'amdahl') {
        obj_input.value = '0.5'
    }else if(obj.value == 'manual') {
        obj_input.value = '2'
    }
}

export default {
    name: 'index',
    data: function() {
        return {
            //page
            firstLevel: 0,
            newSimulationPage: 0,
            // other
            currJobs: this.GLOBAL.schedCore.config.jobs,
            arrivalRate: this.GLOBAL.schedCore.config.arrivalRate,
            serverCluster: this.GLOBAL.schedCore.config.serverCluster,
            algorithmName: this.GLOBAL.schedCore.config.algorithmName,
            algorithm: this.getAl(),
            // file
            myFiles: [] ,
            fileContent: '',

        }
    },
    components: {
        // VueNumericInput
        FilePond
    },
    mounted: function() {
        // window.onbeforeunload = function(event){
        //     return true
        // }
    },
    computed: {
        noJobs: function() {
            return (this.currJobs.length == null || this.currJobs.length == 0)
        },
        noArrivalRate: function() {
            return (this.arrivalRate == null || this.arrivalRate == '' || this.arrivalRate < 0 || isNaN(this.arrivalRate))
        },
        noServerCluster: function() {
            return (this.serverCluster == null || this.serverCluster == '' || this.serverCluster < 0 || isNaN(this.serverCluster))
        },
        noAlgorithm: function() {
            return (this.algorithmName == null || this.algorithmName == '' || this.algorithm == null || this.algorithm == '')
        }
    },
    watch: {
        arrivalRate: function (oldval, newval) {
            if(this.arrivalRate == '' || isNaN(this.arrivalRate)) {
                if(isNaN(oldval)) {
                    this.GLOBAL.schedCore.config.arrivalRate = 1
                }else {
                    this.GLOBAL.schedCore.config.arrivalRate = oldval
                }
            }else {
                this.GLOBAL.schedCore.config.arrivalRate = this.arrivalRate
            }
        },
        serverCluster: function (oldval, newval) {
            if(this.serverCluster == '' || isNaN(this.serverCluster)) {
                if(isNaN(oldval)) {
                    this.GLOBAL.schedCore.config.serverCluster = 16
                }else {
                    this.GLOBAL.schedCore.config.serverCluster = oldval
                }
            }else {
                this.GLOBAL.schedCore.config.serverCluster = this.serverCluster
            }
        },
        algorithmName: function (oldval, newval) {
            this.GLOBAL.schedCore.config.algorithmName = this.algorithmName
        },
        algorithm: function (oldval, newval) {
            this.GLOBAL.schedCore.config.algorithm = this.algorithm
        },


    },
    methods: {

        popUpConfigure: function() {
            var that = this
            const { value: formValues } =  Swal.fire({
                  title: 'Add a new job class',
                  html: '<div style="width:100%;text-align:center;">Select A Job Size Distribution</div>' +
                        '<select class="swal2-select" onchange="option1Change()" id="swal-select1" style="display: flex;"> \
                            <option value="deterministic">Deterministic Dùëíùë°(ùëê)</option> \
                            <option value="uniform">Uniform Uùëõùëñùëì(ùëé,ùëè)</option> \
                            <option value="exponential">Exponential Eùë•ùëù(ùúÜ)</option> \
                            <option value="erlang">Erlang Eùëüùëôùëéùëõùëî(ùëõ,ùúÜ)</option> \
                            <option value="pareto">Pareto Pùëéùëüùëíùë°ùëú(ùõº,ùúé,ùúá)</option> \
                        </select>' +
                        '<input id="swal-input1" class="swal2-input" value="1" placeholder="please input parameters (separated by comma)">' +
                        '<hr/>' +
                        '<div style="width:100%;text-align:center;">Select A Speedup Function</div>' +
                        '<select class="swal2-select" id="swal-select2" style="display: flex;"> \
                            <option value="power">Power function</option> \
                            <option value="amdahl">Amdahl‚Äôs law</option> \
                            <option value="manual">Manual</option> \
                        </select>' +
                        '<input id="swal-input2" class="swal2-input" value="0.5" placeholder="please input parameters (separated by comma)">' ,
                  focusConfirm: false,
                  preConfirm: () => {
                    return [
                      document.getElementById('swal-select1').value,
                      document.getElementById('swal-input1').value,
                      document.getElementById('swal-select2').value,
                      document.getElementById('swal-input2').value
                    ]
                  }
                }).then((result) => {
                  if (result.value) {
                    var job = {
                        'id': that.GLOBAL.schedCore.config.jobs.length,
                        'sizeFun': result.value[0],
                        'sizeFunPara': result.value[1],
                        'speedupFun': result.value[2],
                        'speedupFunPara': result.value[3],
                        'proportionVal': 1,
                        'color':  'rgb(' + (Math.floor(Math.random() * 256)) + ',' + (Math.floor(Math.random() * 256)) + ',' + (Math.floor(Math.random() * 256)) + ')'
                    }
                    that.GLOBAL.schedCore.config.jobs.unshift(job)
                    // const answers = JSON.stringify(result.value)
                    // Swal.fire({
                    //   title: 'All done!',
                    //   html: `
                    //     Your answers:
                    //     <pre><code>${answers}</code></pre>
                    //   `,
                    //   confirmButtonText: 'Done!'
                    // })
                  }
                })
        },

        deleteAJobByIndex: function(index) {
            this.GLOBAL.schedCore.config.jobs.splice(index, 1)
        },
        algorithmNameChange: function() {
            if(this.algorithmName != 'equi') {
                this.algorithm = ''
            }else {
                this.algorithm = this.getAl()
            }
        },
        getFirstPagesByIndex: function (index) {
            // if(index != 2) {
            //     this.firstLevel = index
            // }else {
            //     Swal.fire({
            //       icon: 'error',
            //       title: 'Oops...',
            //       text: 'Under Construction'
            //     })
            // }
            this.firstLevel = index
            this.fileContent = ''
        },

        initialize: function() {
            for(var i = 0; i < this.GLOBAL.schedCore.config.jobs.length; i++) {
                var obj = this.GLOBAL.schedCore.config.jobs[i]
                this.GLOBAL.schedCore.running.jobGeneratorQueue.push(
                    new this.GLOBAL.Objects.JobGenerator(
                        obj
                    )
                )
            }

            // clear
            this.GLOBAL.schedCore.status = false
            this.globalTime = 0
            this.GLOBAL.schedCore.running.queue = []
            this.GLOBAL.schedCore.running.jobsDict = {}
            this.GLOBAL.schedCore.running.completedQueue = []
            this.GLOBAL.schedCore.running.totalCostTimeForCompletedJobs = 0
            this.GLOBAL.schedCore.running.jobGeneratorQueue = []
            this.GLOBAL.schedCore.running.totalJobsNum = 0
            this.GLOBAL.schedCore.running.currentMeanTime = 0
            this.GLOBAL.schedCore.running.availableNodeNum = this.GLOBAL.schedCore.config.serverCluster
            this.GLOBAL.schedCore.running.serverTaskList = []
            this.GLOBAL.schedCore.running.serverLog = []
            this.GLOBAL.schedCore.running.meanTimeLog = []
        },

        getPagesByIndex: function (index) {
            if(index == 1) {
                this.GLOBAL.schedCore.config.jobs = this.currJobs
                if(this.recalculateLamda()) {
                    this.newSimulationPage = index
                }else {
                    alert("Please type a positive number for Proportion")
                }

            }else if(index < 4) {
                this.newSimulationPage = index
            }else {


                this.initialize()
                this.$router.push({ name: "Simulation"})


                // const answers = JSON.stringify(this.GLOBAL.schedCore.config)
                // Swal.fire({
                //   icon: 'error',
                //   title: 'Under Construction',
                //   html: `<div style="width:400px;overflow-x:scroll">Your configure is: <pre><code>${answers}</code></pre></div>`
                // })
            }
        },

        recalculateLamda: function () {
            var maxPro = -1
            var maxRate = this.GLOBAL.schedCore.config.arrivalRate
            var jobs = this.GLOBAL.schedCore.config.jobs
            for(var i = 0; i < jobs.length; i++) {
                if(jobs[i].proportionVal <= 0) return false
                if(maxPro < jobs[i].proportionVal) {
                    maxPro = jobs[i].proportionVal
                }
            }
            for(var j = 0; j < jobs.length; j++) {
                jobs[j].proportionVal = maxRate * (jobs[j].proportionVal / maxPro)
            }
            return true
        },

        handleFileAdded: function() {
            var fileArr = this.$refs.pond.getFiles()
            if(fileArr.length > 0) {
                var file = this.$refs.pond.getFiles()[0].file
                var read = new FileReader()
                read.readAsBinaryString(file)
                var that = this
                read.onloadend = function(){
                    that.fileContent = read.result
                    that.processUploadFile()
                    that.fileContent = ''
                }
            }else {
                alert("please upload file!")
            }

            // console.log(this.myFiles)
        },
        handleFilePondInit: function() {


            // FilePond instance methods are available on `this.$refs.pond`
        },
        handleFileRemove: function() {
                this.fileContent = ""
        },
        processUploadFile: function() {
            try {
                var obj = JSON.parse(this.fileContent)
                // add new queue
                var newQueue = []
                for(var i = 0; i < obj.running.queue.length; i++) {
                    var job_id_i = obj.running.queue[i].jobClass + '_' + obj.running.queue[i].id
                    newQueue.push(obj.running.jobsDict[job_id_i])
                }
                var newCompletedQueue = []
                for(var j = 0; j < obj.running.completedQueue.length; j++) {
                    var job_id_j = obj.running.completedQueue[j].jobClass + '_' + obj.running.completedQueue[j].id
                    newCompletedQueue.push(obj.running.jobsDict[job_id_j])
                }
                var newServerTaskList = []
                for(var k = 0; k < obj.running.serverTaskList.length; k++) {
                    var job_id_k = obj.running.serverTaskList[k].jobClass + '_' + obj.running.serverTaskList[k].id
                    newServerTaskList.push(obj.running.jobsDict[job_id_k])
                }
                obj.running.queue = newQueue
                obj.running.completedQueue = newCompletedQueue
                obj.running.serverTaskList = newCompletedQueue
                this.GLOBAL.schedCore = obj
                this.$router.push({ name: "Simulation"})

            } catch (e) {
                alert("The file is not a JSON string or lost some fileds, please see the console")
                // console.log(e)
            }
        },



        getAl: function() {
            return `

            function shedulingAlgorithm(queue, serverInfo, jobsDict, completedQueue, jobGeneratorQueue, currentTime) {

                serverInfo.serverTaskList.sort(function(a, b){
                    return a.leftTime - b.leftTime
                })
                var doneJobs = []
                var allowRunTime = 0
                var currentAvailableNodeNum = serverInfo.availableNodeNum
                if(serverInfo.serverTaskList.length != 0) {
                    doneJobs.push(serverInfo.serverTaskList[0]);
                    for(var i = 1; i < serverInfo.serverTaskList.length; i++) {
                        if(serverInfo.serverTaskList[0].leftTime == serverInfo.serverTaskList[i].leftTime) {
                            doneJobs.push(serverInfo.serverTaskList[i])
                        }else {
                            break;
                        }
                    }
                }
                if(doneJobs.length != 0) {
                    allowRunTime = doneJobs[0].leftTime
                }
                for(var j = 0; j < doneJobs.length; j++) {
                    currentAvailableNodeNum += doneJobs[j].usedNodeNumber
                    var currJob = doneJobs[0]
                    currJob.currentSize = 0
                    currJob.leftTime = 0
                    currJob.usedNodeNumber = 0
                    currJob.speedup = 0
                    currJob.completedTime = currentTime + allowRunTime
                    currJob.log.push({
                        time: currentTime + allowRunTime,
                        size: 0
                    })
                    schedCore.running.totalCostTimeForCompletedJobs += currJob.completedTime - currJob.generateTime
                    schedCore.running.currentMeanTime = getMeanTime()

                    schedCore.running.meanTimeLog.push(
                        {
                            totalTime: schedCore.running.totalCostTimeForCompletedJobs,
                            num: schedCore.running.completedQueue.length,
                            meanTime: schedCore.running.currentMeanTime
                        }
                    )
                    completedQueue.push(currJob)
                    serverInfo.serverTaskList.shift()
                }
                // handle remaining jobs in the task list
                for(var k = 0; k < serverInfo.serverTaskList.length; k++) {
                    var currJob_1 = serverInfo.serverTaskList[k]
                    currJob_1.currentSize -= currJob_1.speedup * allowRunTime
                    currJob_1.leftTime = currJob_1.currentSize / currJob_1.speedup
                }
                var newJobs = []
                var newJobsObj = getNewJobs(jobGeneratorQueue, allowRunTime)

                newJobs = newJobsObj.newJobs
                queue = queue.concat(newJobs)

                // add new jobs to queue

                while(currentAvailableNodeNum > 0){
                    if(queue.length > 0) {
                        serverInfo.serverTaskList.push(queue[0])
                        queue.shift()
                        currentAvailableNodeNum -= 1
                    }else {
                        break
                    }
                }

                currentTime += allowRunTime
                if(currentTime < newJobsObj.currentTime) {
                    currentTime = newJobsObj.currentTime
                }

                // reassign variable for jobs
                if(serverInfo.serverTaskList.length != 0) {
                    currentAvailableNodeNum = 0
                    var mod = serverInfo.serverNum % serverInfo.serverTaskList.length
                    var nodeNum = Math.floor(serverInfo.serverNum / serverInfo.serverTaskList.length)

                    for(var l = 0; l < serverInfo.serverTaskList.length; l++) {
                        var currJob_2 = serverInfo.serverTaskList[l]
                        currJob_2.usedNodeNumber = nodeNum
                        currJob_2.speedup = speedUpFunc(currJob_2.speedupFun, currJob_2.speedupFunPara, nodeNum)
                        currJob_2.leftTime = currJob_2.currentSize / currJob_2.speedup
                        currJob_2.log.push({
                            time: currentTime,
                            size: currJob_2.currentSize,
                        })
                    }
                    if(mod != 0) {
                        serverInfo.serverTaskList[0].usedNodeNumber += mod
                        serverInfo.serverTaskList[0].speedup = speedUpFunc(serverInfo.serverTaskList[0].speedupFun, serverInfo.serverTaskList[0].speedupFunPara, serverInfo.serverTaskList[0].usedNodeNumber)
                    }
                }
                // console.log(serverInfo.serverTaskList)



                var slog = {
                    time: currentTime,
                    usedNumber: serverInfo.serverNum - currentAvailableNodeNum,
                    list: []
                }
                for(var q = 0; q < serverInfo.serverTaskList.length; q++) {
                    slog.list.push({
                        name: serverInfo.serverTaskList[q].name,
                        number: serverInfo.serverTaskList[q].usedNodeNumber
                    })
                }
                schedCore.running.serverLog.push(slog)
                var res = {
                    queue: queue,
                    serverInfo: {
                            serverTaskList: serverInfo.serverTaskList,
                            serverNum: serverInfo.serverNum,
                            availableNodeNum: currentAvailableNodeNum,
                    },
                    jobsDict: jobsDict,
                    completedQueue: completedQueue,
                    jobGeneratorQueue: jobGeneratorQueue,
                    currentTime: currentTime,
                    serverLog: schedCore.running.serverLog
                }
                return res

            }
            `;
        }



    }
}
</script>

